.signup-page {
	width: 100%;
	height: 100vh;
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: row;
	padding: 40px;
}


.square-top {
	width: 130px;
	height: 130px;
	position: absolute;
	top: 0;
	right: 0;
	background: #0b2442;
}


.signup-page__img {
	width: 67%;
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	background: #427bd6;
	color: #fff;

}

.signup-page__img p {
	font-size: 3rem;
	color: #fff;
	font-weight: bold;
}

.signup-page h1 {
	color: #427bd6;
	font-size: 1.8rem;
	position: absolute;
	top: 10px;
}

.signup-page__form {
	width: 33%;
	height: 80%;
	padding: 5px 8px;
	position: relative;
	left: -40px;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	border: 1px solid #ddd;
	background: #fff;
	box-shadow: 5px 5px 5px #ddd;
	z-index: 1;
}

.signup-page__form label {
	color: #427bd6;
	font-size: 12px;
}


.signup-page__form-inputs {
	position: relative;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
}

.signup-page__form input {
	width: 90%;
	height: 34px;
	position: relative;
	margin: 6px 0;
	border: 1px solid #ddd;
	padding: 0 6px;
	border-radius: 2px;
}

.signup-page__form button {
	position: absolute;
	bottom: 12px;
	background: #427bd6;
	border: 1px solid #427bd6;
	color: #fff;
	font-weight: bold;
	letter-spacing: 1px;
	padding: 3px 9px;
	width: 50%;
}

.signup-page__inf {
	width: 90%;
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: row;
}

 .signup-page__policy {
	 font-size: 8px;
	 color: #ccc;
	 display: flex;
	 align-items: center;
	 justify-content: center;
	 flex-direction: row;
	 position: absolute;
	 left: 0;
	 bottom: -15px;
 }

 .signup-page__policy input {
	 width: 12px;
	 height: 12px;
	 color: #ccc;
 }

 .signup-page__haveaccount {
		position: absolute;
		font-size: 8px;
		color: #ccc;
		cursor: pointer;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: row;
		right: 0;
		bottom: -8px;
 }

 .error {
	 font-size: 10px;
	 color: red;
 }
